  &:hover,
  &:focus {
    // opacity: 0.6;

    .search__icon {
      fill: rgba(255,255,255,0.6);
    }
  }
}

.search__icon {
  position: absolute;

  top: 0;
  left: 0;

  fill: #fff;
  transition: @s;

  &--search {
    z-index: 0;
    opacity: 1;
  }
  &--close {
    z-index: -1;
    opacity: 0;
  }

  // &:hover,
  // &:focus {
  //   fill: rgba(255,255,255,0.6);
  // }
}

.active {
  .search__icon {
    &--search {
      z-index: -1;
      opacity: 0;
    }
    &--close {
      z-index: 0;
      opacity: 1;
    }
  }
}

.search-toggle,
.search__input {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  outline: none;
}

.search__form {
  position: relative;
  // .o;

  width: 1055px;
}

.search__input {
  box-sizing: border-box;
  width: 100%;

  border-bottom: 0.1rem solid #413B4A;

  color: inherit;

  // &::-webkit-input-placeholder,
  // &::-moz-placeholder,
  // &:-moz-placeholder,
  // &:-ms-input-placeholder,
  &::placeholder {
    opacity: 1;
    color: inherit;
  }
}

.search__label-icon {
  position: absolute;
  // top: 1rem;
  // right: 1.7rem;
  cursor: pointer;

  content: '';
  position: absolute;
  top: 0;
  left: -38px;

  width: 23px;
  height: 23px;

  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url('../img/svg/icon-search.svg');

}

.search__datalist-wrap {
  // display: none;
  box-sizing: border-box;

  position: absolute;

  z-index: -1;
  opacity: 0;
  visibility: hidden;

  top: 49px;
  left: -40px;
  // width: 100%;
  width: 1178px;

  padding: 23px 40px;

  background: #413B4A;
  border-radius: 0px 0px 58px 58px;

  transition: @s;

  &.active {
    z-index: 0;
    opacity: 1;
    visibility: visible;
  }
}

.search__datalist {
  font-size: 18px;
}

.search__datalist-item {
  margin-bottom: 15px;
}

.search__datalist-link {

}

@media (max-width: @mobile-width) {
  .search__input {
    .padding-block(0.9rem);
    .padding-inline(1.5rem, 4.5rem);

    font-size: 1.6rem;
    line-height: 2.3rem;

    -moz-appearance: none;
 -webkit-appearance: none;
         appearance: none;
    outline: none;
  }

  .search__icon {
    top: 50%;
    transform: translateY(-50%);
    right: @m10px;
  }
}
